.rootContainer {
    width: 670px;
    max-height: 487px;
    background-color: #FFFFFF;
    font-family: 'Inter';
    font-style: normal;
    border: 0px;
    border-radius: 6px;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    display: flex;
    padding: 15px;
    justify-content: center;
}

.subRootContainer {
    width: 606px;
}

.container {
    width: 606px;
    height: 210px;
    border: 1px dashed rgba(59, 125, 221, 0.2);
    border-radius: 6px;
    background-color: rgba(59, 125, 221, 0.02);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}


.drag {
    display: flex;
    margin-bottom: -20px;
}


.newHead h3 {
    font-weight: 700;
font-size: 20px;
line-height: 130%;
color: #231C16;

}

.options p {
    color: #231C16;
    font-weight: 600;
    font-size: 13px;
   
}

.newHead img {
    padding: 0 10px; 
    width: 19px;
    height: 24px;
}

.sampleDownload {
    color: #3B7DDD !important;
    text-decoration: underline;
    cursor: pointer;
    font-weight: 400;
    font-size: 13px;
}


.btn button {
    border: 0px;
}

.btnContainer button {
    border: 0px;
}

.continue {
  color: #FFFFFF;
  background-color: #0EB198;
  border-radius: 6px;
  padding: 10px;
  font-weight: 600;
font-size: 14px;
cursor: pointer;
margin-right: 10px;
}

.continueDisabled {
    color: #FFFFFF;
  border-radius: 6px;
  padding: 10px;
  font-weight: 600;
font-size: 14px;
margin-right: 10px;
    background-color: #64ffe8;
}

.cancel {
    color: #231C16;
    font-weight: 600;
    font-size: 14px;
    background-color: transparent;
    cursor: pointer;
}

.radio {
    display: flex;
    width: 65%;
    display: flex;
    justify-content: space-between;
    font-weight: 400;
font-size: 14px;
color: #231C16;

}

.newRadio {
    display: flex;
    width: 100%;
    display: flex;
    justify-content: space-between;
    font-weight: 400;
   font-size: 14px;
   color: #231C16;

}

