table {
    font-family: arial, sans-serif;
    border-collapse: collapse;
    width: 100%;
    margin: 2rem 0rem;
    text-align: left;
    
    
  }
  
  td, th {
    padding: 1rem 2rem ; 
    
  }
  th{
    background-color: #F9FBFC;
    font-family: 'Inter';
font-style: normal;
font-weight: 600;
font-size: 13px;
  }

  tr{
    border: 1px solid #DCDEDE;
    font-family: 'Inter';
font-style: normal;
font-weight: 400;
font-size: 14px;
  }