.rootContainer {
    min-width: 670px;
    width: 80%;
    max-height: 487px;
    background-color: #FFFFFF;
    font-family: 'Inter';
    font-style: normal;
    border: 0px;
    border-radius: 6px;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    display: flex;
    padding: 15px;
    justify-content: center;
}

.subRootContainer {
    width: 606px;
}

.inputContainer {
    margin: 15px 0;
}

.inputContainer input {
    width: 500px;
    padding: 10px;
    border-radius: 5px;
    border: 1px solid rgb(109, 109, 109);
    outline: none;
    transition: 0.25s;
}

.inputContainer input:focus {
    border: 1px solid #231C16;
}

.btnContainer {
    padding: 15px 0;
}

.btnContainer button {
    border: 0px;
}

.continueBtn {
    color: #FFFFFF;
    background-color: #0EB198;
    border-radius: 6px;
    padding: 10px;
    font-weight: 600;
    font-size: 14px;
    cursor: pointer;
    margin-right: 10px;
}

.cancelBtn {
    color: #231C16;
    font-weight: 600;
    font-size: 14px;
    background-color: transparent;
    cursor: pointer;
}