.rootContainer {
    width: 670px;
    max-height: 487px;
    background-color: #FFFFFF;
    font-family: 'Inter';
    font-style: normal;
    border: 0px;
    border-radius: 6px;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    display: flex;
    justify-content: center;
}

.container {
    width: 606px;
    height: 210px;
    border: 1px dashed rgba(59, 125, 221, 0.2);
    border-radius: 6px;
    background-color: rgba(59, 125, 221, 0.02);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.options p {
    color: #231C16;
    font-weight: 600;
    font-size: 13px;
   
}

.sampleDownload {
    color: #3B7DDD !important;
    text-decoration: underline;
    cursor: pointer;
    font-weight: 400;
    font-size: 13px;
}

.radio {
    display: flex;
    width: 65%;
    display: flex;
    justify-content: space-between;
    font-weight: 400;
    font-size: 14px;
    color: #231C16;
}

.newRadio {
    display: flex;
    width: 100%;
    display: flex;
    justify-content: space-between;
    font-weight: 400;
    font-size: 14px;
    color: #231C16;
}

