.headTxt {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.headTxt h3 {
    font-weight: 700;
    font-size: 20px;
    line-height: 130%;
    color: #231C16;
}

.headTxt img {
    padding: 0 10px; 
    width: 19px;
    height: 24px;
    cursor: pointer;
}