.btnContainer {
    padding: 5px 0;
}

.btnContainer button {
    border: 0px;
}

.continueBtn {
    color: #FFFFFF;
    background-color: #0EB198;
    border-radius: 6px;
    padding: 10px;
    font-weight: 600;
    font-size: 14px;
    cursor: pointer;
    margin-right: 10px;
}

.continueBtnDisabled {
    color: #FFFFFF;
    border-radius: 6px;
    padding: 10px;
    font-weight: 600;
    font-size: 14px;
    margin-right: 10px;
    background-color: #64ffe8;
}

.cancelBtn {
    color: #231C16;
    font-weight: 600;
    font-size: 14px;
    background-color: transparent;
    cursor: pointer;
}