.importedData {
     font-family: 'Inter';
font-style: normal;
width: 100%;
}

.container {
    width: 100%;
    display: flex;
    justify-content: center;
}

.mainContainer {
    width: 90%;
}

.head {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.mainHead {
    display: flex;
    width: fit-content;
    justify-content: space-between;
}

.mainHead img{
    padding-right: 7px;
}
.mainHead h2 {
    padding-left: 7px;
}

.submitButton {
    background-color: #0EB198;
    border: 0px;
    border-radius: 6px;
    padding: 10px;
    color: #FFFFFF;
    font-weight: 600;
font-size: 13px;
cursor: pointer;
}
.alertBox{
    border: 1px solid #3B7DDD;
    border-radius: 0.5rem;
    background-color: #F8FAFD;
    display: flex;
    padding: 1rem;
    align-items: center;
    width: 44%;
    font-family: 'Inter';
font-style: normal;
font-weight: 400;
font-size: 13px;
}
.infoIcon{
 margin: 0rem 0.8rem;
}
.links{
    color: #3B7DDD !important;
    text-decoration: underline;
    cursor: pointer;   
    margin: 0rem 0.5rem;
}
.textContainer{
    display: flex;
    align-items: center;   
}
.downloadBtn{
    font-family: 'Inter';
font-style: normal;
font-weight: 400;
font-size: 13px;
color: #231C16;
border: 1px solid #DCDEDE;
border-radius: 6px;
padding: 8px 12px;
background-color: white;
margin: 0rem 1.5rem;
cursor: pointer;
display: flex;
}
.resultsDropdown {
    border:1px solid #8A8A8A;
    padding: 0.35rem;
    outline: 0;
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 13px;
    color: #231C16;
    border-radius: 6px;
    margin: 0rem 0.8rem 0rem 0rem;
    cursor: pointer;
}
.dropdownItem{
    cursor: pointer;
   
}
.generatItems{
    display: flex;
    
}
.excelIcon{
    margin-right: 0.5rem;
}
.modalContainer{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 15;
}

.greyoutBg {
    position: fixed;
    top: 0;
    left: 0;
    background-color: #000;
    opacity: 0.5;
}